<template >
  <div>
    <AppBar />
    <v-content>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col>
            <div style="height: 700px; width: 100%">
              <l-map
                v-if="showMap"
                :zoom="zoom"
                :center="center"
                :options="mapOptions"
                style="height: 80%"
                @update:center="centerUpdate"
                @update:zoom="zoomUpdate"
              >
                <l-tile-layer :url="url" :attribution="attribution" />
                <l-marker
                  v-for="marker in markers"
                  :icon="marker.icon"
                  v-bind:key="marker.id"
                  :lat-lng="marker.latLng"
                >
                  <l-popup>
                    <h2>{{marker.nombre}}</h2>
                    <h4>{{ marker.sub }}</h4>
                  </l-popup>
                </l-marker>
              </l-map>
            </div>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
    <v-footer app dark style="z-index: 999">
      <span>&copy; 2020</span>
    </v-footer>
  </div>
</template> 

<script>
import AppBar from "@/components/AppBar.vue";
import apiOrquesta from "@/services/apiOrquesta.js";
import { latLng, icon } from "leaflet";
import { LMap, LTileLayer, LMarker, LPopup } from "vue2-leaflet";
export default {
  title: "OEB - Mapa",
  name: "Mapa",
  data() {
    return {
      zoom: 13,
      center: latLng(-34.920915, -57.95514),
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      attribution:
        '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      currentZoom: 11.5,
      currentCenter: latLng(47.41322, -1.219482),
      showParagraph: false,
      violin: icon({
        iconUrl: require("@/assets/violin.png"),
        iconSize: [32, 37],
        iconAnchor: [16, 37]
      }),
      escuela: icon({
        iconUrl: require("@/assets/escuela.png"),
        iconSize: [32, 37],
        iconAnchor: [16, 37]
      }),
      mapOptions: {
        zoomSnap: 0.5
      },
      showMap: true,
      markers: [],
      instrumentos: [],
      nucleos: [],
      estudiantes: [{ id: "0", nombre: "Administracion" }]
    };
  },
  components: {
    AppBar,
    LMap,
    LTileLayer,
    LMarker,
    LPopup
  },
  async created() {
    await this.cargarInstrumentos();
    this.agregarMarkers();
  },
  methods: {
    async cargarInstrumentos() {
      await apiOrquesta
        .getInstrumentos()
        .then(response => (this.instrumentos = response.data));
      await apiOrquesta
        .getNucleos()
        .then(response => (this.nucleos = response.data));
      await apiOrquesta.getEstudiantes().then((response) =>
        response.data.forEach((estudiante) => {
          this.estudiantes.push(estudiante);
        })
      );
    },
    agregarMarkers() {
      this.instrumentos.forEach(instrumento => {
        this.markers.push({
          nombre: instrumento.nombre + " n°" + instrumento.n_inventario,
          sub: this.getEstudiante(instrumento.id_alumno),
          latLng: latLng(instrumento.latitud, instrumento.longitud),
          icon: icon({
            iconUrl: "https://grupo19.proyecto2019.linti.unlp.edu.ar/uploads/" + instrumento.n_inventario + ".jpg",
            iconSize: [37, 37],
            iconAnchor: [16, 37],
          })
        });
      });
      this.nucleos.forEach(nucleo => {
        this.markers.push({
          nombre: nucleo.nombre,
          sub: '',
          latLng: latLng(nucleo.latitud, nucleo.longitud),
          icon: this.escuela
        });
      });
    },
    zoomUpdate(zoom) {
      this.currentZoom = zoom;
    },
    centerUpdate(center) {
      this.currentCenter = center;
    },
    getEstudiante(idE) {
      if (idE == "0") {
        return "Administracion";
      } else {
        var aux = this.estudiantes.filter(function(element) {
          return element.id == idE;
        });
        return aux[0].nombre + " " + aux[0].apellido;
      }
    }
  }
};
</script>