import axios from 'axios'
const apiClient = axios.create({
  baseURL: `https://api-referencias.proyecto2019.linti.unlp.edu.ar/`,
  withCredentials: false, // This is the default
  headers: {}
})

export default {
    getTipoDocs() {
      return apiClient.get("tipo-documento")
    },
    getLocalidades() {
        return apiClient.get("localidad")
    }
}