<template>
  <div>
    <AppBar />
    <v-content>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col>
            <v-form ref="form" v-model="valid">
              <span class="headline"> Configuracion </span>
              <v-switch
                v-model="configuracion.habilitado"
                class="ma-4"
                label="Habilitado"
              ></v-switch>
              <v-text-field
                v-model="configuracion.titulo"
                :rules="tituloRules"
                label="Titulo"
                required
              ></v-text-field>
              <v-text-field
                v-model="configuracion.descripcion"
                :rules="descripcionRules"
                label="Descripcion"
                required
              ></v-text-field>
              <v-text-field
                v-model="configuracion.contacto"
                :rules="contactoRules"
                label="Contacto"
                required
              ></v-text-field>
              <v-text-field
                type="number"
                v-model="configuracion.cantidadfilas"
                :rules="cantidadFilasRules"
                label="Cantidad de filas"
                required
              ></v-text-field>
              <v-btn
                v-if="permisos.includes('configuracion_configurar')"
                :disabled="!valid"
                color="success"
                class="mr-4"
                @click="save"
              >
                Cargar
              </v-btn>
            </v-form>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
    <v-footer app dark>
      <span>&copy; 2020</span>
    </v-footer>
  </div>
</template>

<script>
import apiOrquesta from "@/services/apiOrquesta.js";
import AppBar from "@/components/AppBar.vue";

export default {
  title: "OEB - Configuracion",
  name: "Configuracion",
  data() {
    return {
      valid: false,
      configuracion: {
        habilitado: false,
        titulo: "",
        descripcion: "",
        contacto: "",
        cantidadfilas: 0,
      },
      permisos: [],
      tituloRules: [(v) => !!v || "titulo es requerido"],
      descripcionRules: [(v) => !!v || "Descripcion es requerido"],
      contactoRules: [(v) => !!v || "contacto es requerido"],
      cantidadFilasRules: [(v) => !!v || "cantidad de filas es requerido"],
    };
  },
  async created() {
    await this.cargarInfo();
    this.cargarPermisos();
  },
  components: {
    AppBar,
  },
  methods: {
    cargarInfo() {
      apiOrquesta
        .getInfo()
        .then((response) => (this.configuracion = response.data[0]));
    },
    async save() {
      await apiOrquesta.loadInfo(this.configuracion);
      this.cargarInfo();
    },
    cargarPermisos() {
      var aux = this.$store.state.user.permisos;
      for (var i = 0; i < aux.length; i++) {
        if (aux[i].nombre.startsWith("configuracion")) {
          this.permisos.push(aux[i].nombre);
        }
      }
    },
  },
};
</script>
