<template>
  <div>
    <AppBar />
    <v-content>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col>
            <v-data-table :headers="headers" :items="estudiantes" sort-by="nombre" class="elevation-1" dark >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>Estudiantes</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <v-spacer></v-spacer>
                  <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on }">
                      <v-btn color="primary" dark class="mb-2" v-on="on">+</v-btn>
                    </template>
                    <v-card>
                      <v-card-title>
                        <span class="headline"> {{ formTitle }} </span>
                      </v-card-title>
                      <v-card-text>
                        <v-container>
                          <v-form ref="form" v-model="valid">
                            <v-row>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.nombre" label="nombre" :rules="nombreRules" required ></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.apellido" label="Apellido" :rules="apellidoRules" required ></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-select v-model="editedItem.tipo_doc_id" :rules="tipoRules" :items="tipos_doc" item-text="nombre" item-value="id" label="tipo doc" required ></v-select>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.numero" label="numero Doc" :rules="docRules" required ></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-menu ref="menu1" v-model="menu1" :close-on-content-click="false" transition="scale-transition" offset-y max-width="290px" min-width="290px" >
                                  <template v-slot:activator="{ on }">
                                    <v-text-field v-model="editedItem.fecha_nac" label="Date" hint="MM/DD/YYYY format" persistent-hint v-on="on" ></v-text-field>
                                  </template>
                                  <v-date-picker v-model="editedItem.fecha_nac" no-title @input="menu1 = false" :rules="fechaRules" required ></v-date-picker>
                                </v-menu>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.tel" label="Celular" :rules="telRules" required ></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-select v-model="editedItem.localidad_id" :rules="localidadRules" :items="localidades" item-text="nombre" item-value="id" label="Localidad" required ></v-select>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.domicilio" label="Domicilio" :rules="domicilioRules" required ></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-select v-model="editedItem.genero_id" :rules="tipoRules" :items="generos" item-text="nombre" item-value="id" label="Genero" required ></v-select>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-select v-model="editedItem.barrio_id" :rules="tipoRules" :items="barrios" item-text="nombre" item-value="id" label="Barrio" required ></v-select>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-select v-model="editedItem.escuela_id" :rules="tipoRules" :items="escuelas" item-text="nombre" item-value="id" label="Escuela" required ></v-select>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-select v-model="editedItem.nivel_id" :rules="tipoRules" :items="niveles" item-text="nombre" item-value="id" label="Nivel" required ></v-select>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.lugar_nac" label="Oriundo" :rules="domicilioRules" required ></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-select v-model="editedItem.responsable" :rules="tipoRules" :items="responsables" item-text="nombre" item-value="id" label="Responsable" required ></v-select>
                              </v-col>
                            </v-row>
                          </v-form>
                        </v-container>
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                        <v-btn v-if="permisos.includes('estudiante_crear')" :disabled="!valid" color="blue darken-1" text @click="save" >Cargar</v-btn >
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-toolbar>
              </template>
              <template v-slot:item.tipo_doc_id="{ item }">
                {{ getTipo_doc(item.tipo_doc_id) }}
              </template>
              <template v-slot:item.localidad_id="{ item }">
                {{ getLocalidad(item.localidad_id) }}
              </template>
              <template v-slot:item.genero_id="{ item }">
                {{ getGenero(item.genero_id) }}
              </template>
              <template v-slot:item.barrio_id="{ item }">
                {{ getBarrio(item.barrio_id) }}
              </template>
              <template v-slot:item.escuela_id="{ item }">
                {{ getEscuela(item.escuela_id) }}
              </template>
              <template v-slot:item.nivel_id="{ item }">
                {{ getNivel(item.nivel_id) }}
              </template>
              <template v-slot:item.responsable="{ item }">
                {{ getResponsable(item.responsable) }}
              </template>
              <template v-slot:item.actions="{ item }">
                <v-icon v-if="permisos.includes('estudiante_update')" small class="mr-2" @click="editItem(item)" dark >mdi-pencil</v-icon >
                <v-icon v-if="permisos.includes('estudiante_borrar')" small @click="deleteItem(item)" dark >mdi-delete</v-icon >
              </template>
            </v-data-table>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
    <v-footer app dark>
      <span>&copy; 2020</span>
    </v-footer>
  </div>
</template>

<script>
import apiOrquesta from "@/services/apiOrquesta.js";
import apiCatedra from "@/services/apiCatedra.js";
import AppBar from "@/components/AppBar.vue";

export default {
  title: "OEB - Estudiantes",
  name: "Estudiantes",
  data() {
    return {
      dateFormatted: this.formatDate(new Date().toISOString().substr(0, 10)),
      menu1: false,
      valid: false,
      dialog: false,
      headers: [
        { text: "Nombre", align: "start", value: "nombre" },
        { text: "Apellido", align: "start", value: "apellido" },
        { text: "Nacimiento", align: "start", value: "fecha_nac" },
        { text: "Tipo doc", align: "start", value: "tipo_doc_id" },
        { text: "Numero doc", align: "start", value: "numero" },
        { text: "Celular", align: "start", value: "tel" },
        { text: "Localidad", align: "start", value: "localidad_id" },
        { text: "Domicilio", align: "start", value: "domicilio" },
        { text: "Genero", align: "start", value: "genero_id" },
        { text: "Barrio", align: "start", value: "barrio_id" },
        { text: "Escuela", align: "start", value: "escuela_id" },
        { text: "Nivel", align: "start", value: "nivel_id" },
        { text: "Responsable", align: "start", value: "responsable" },
        { text: "Oriundo", align: "start", value: "lugar_nac" },
        { text: "Acciones", value: "actions", sortable: false },
      ],
      estudiantes: [],
      localidades: [],
      permisos: [],
      tipos_doc: [],
      generos: [],
      escuelas: [],
      niveles: [],
      barrios: [],
      responsables: [
        { id: "1", nombre: "Padre" },
        { id: "2", nombre: "Madre" },
        { id: "3", nombre: "Otro" },
      ],
      editedItem: { id: -1, apellido: "", nombre: "", fecha_nac: "", localidad_id: 0, domicilio: "", tipo_doc_id: 0, numero: "", tel: "", lugar_nac: "", genero_id: 0, barrio_id: 0, escuela_id: 0, nivel_id: 0, responsable: 0, },
      defaultItem: { id: -1, apellido: "", nombre: "", fecha_nac: "", localidad_id: 0, domicilio: "", tipo_doc_id: 0, numero: "", tel: "", lugar_nac: "", genero_id: 0, barrio_id: 0, escuela_id: 0, nivel_id: 0, responsable: 0, },
      
      editedIndex: -1,
      
      nombreRules: [(v) => !!v || "Nombre es requerido"],
      apellidoRules: [(v) => !!v || "Apellido es requerido"],
      tipoRules: [(v) => !!v || "Tipo de documento es requerido"],
      docRules: [(v) => !!v || "Documento es requerido"],
      fechaRules: [(v) => !!v || "Fecha es requerida"],
      telRules: [(v) => !!v || "Telefono es requerido"],
      localidadRules: [(v) => !!v || "Localidad es requerida"],
      domicilioRules: [(v) => !!v || "Domicilio es requerido"],
    };
  },
  computed: {
    formTitle() {
      return this.editedItem.id === -1
        ? "Agregar estudiantes"
        : "Editar estudiantes";
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
  },
  async created() {
    await this.cargarDocumentos();
    await this.cargarEstudiantes();
    await this.cargarPermisos();
  },
  components: {
    AppBar,
  },
  methods: {
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${month}/${day}/${year}`;
    },
    parseDate(date) {
      if (!date) return null;
      const [month, day, year] = date.split("/");
      return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
    },
    cargarEstudiantes() {
      apiOrquesta
        .getEstudiantes()
        .then((response) => (this.estudiantes = response.data));
    },
    cargarDocumentos() {
      apiCatedra
        .getTipoDocs()
        .then((response) => (this.tipos_doc = response.data));
      apiCatedra
        .getLocalidades()
        .then((response) => (this.localidades = response.data));
      apiOrquesta
        .getBarrios()
        .then((response) => (this.barrios = response.data));
      apiOrquesta
        .getEscuelas()
        .then((response) => (this.escuelas = response.data));
      apiOrquesta
        .getNiveles()
        .then((response) => (this.niveles = response.data));
      apiOrquesta
        .getGeneros()
        .then((response) => (this.generos = response.data));
    },
    getTipo_doc(tipo) {
      if (tipo.id) {
        return this.tipos_doc[tipo.id - 1].nombre;
      } else {
        return this.tipos_doc[tipo - 1].nombre;
      }
    },
    getLocalidad(loc) {
      if (loc.id) {
        return this.localidades[loc.id - 1].nombre;
      } else {
        return this.localidades[loc - 1].nombre;
      }
    },
    getBarrio(idb) {
      return this.barrios[idb - 1].nombre;
    },
    getEscuela(idE) {
      return this.escuelas[idE - 1].nombre;
    },
    getNivel(idN) {
      return this.niveles[idN - 1].nombre;
    },
    getGenero(idG) {
      return this.generos[idG - 1].nombre;
    },
    getResponsable(idR) {
      var aux = this.responsables.filter(function(element) {
        return element.id == idR;
      })[0];
      if (aux) {
        return aux.nombre;
      } else {
        return "actualizar";
      }
    },
    editItem(item) {
      this.editedIndex = this.estudiantes.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
      this.editedItem.localidad_id = this.localidades[item.localidad_id - 1];
      this.editedItem.tipo_doc_id = this.tipos_doc[item.tipo_doc_id - 1];
      this.editedItem.responsable = this.responsables[item.responsable -1];
      this.dateFormatted = this.formatDate(
        new Date(item.fecha_nac).toISOString().substr(0, 10)
      );
      this.editedItem.fecha_nac = this.parseDate(this.dateFormatted);
    },
    deleteItem(item) {
      const index = this.estudiantes.indexOf(item);
      confirm("Are you sure you want to delete this Estudiantes?") &&
        apiOrquesta.deleteEstudiante(item.id).then(() => {
           this.estudiantes.splice(index, 1);
        });
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
      });
    },
    async save() {
      this.$store.commit('activateLoading')
      if (this.editedItem.id > -1) {
        await apiOrquesta.modificarEstudiante(this.editedItem)
        .then(() => {
          this.editedItem.localidad_id = this.editedItem.localidad_id.id
          this.editedItem.tipo_doc_id = this.editedItem.tipo_doc_id.id
          Object.assign(this.estudiantes[this.editedIndex], this.editedItem);   
        })
        .catch((error) => {
          console.log(error)
          this.$store.commit('activeSnack', 'Ocurrio un error, intente de nuevo')
          this.close();
        })
      } else {
        await apiOrquesta.createEstudiante(this.editedItem).then(() => {
          this.editedItem.id = this.estudiantes.length + 1         
          this.estudiantes.push(this.editedItem);
        })
        .catch((error) => {
          console.log(error)
          this.$store.commit('activeSnack', 'Ocurrio un error, intente de nuevo')
          this.close();
        })
      }
      this.close();
      this.$store.commit('deactivateLoading')
    },
    cargarPermisos() {
      var aux = this.$store.state.user.permisos;
      for (var i = 0; i < aux.length; i++) {
        if (aux[i].nombre.startsWith("estudiante")) {
          this.permisos.push(aux[i].nombre);
        }
      }
    },
  },
};
</script>
