<template>
  <div>
    <AppBar />
    <v-content>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col>
            <v-data-table :headers="headers" :items="instrumentos" sort-by="nombre" class="elevation-1" dark>
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>Instrumentos</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <v-spacer></v-spacer>
                  <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on }">
                      <v-btn color="primary" dark class="mb-2" v-on="on">+</v-btn>
                    </template>
                    <v-card>
                      <v-card-title>
                        <span class="headline">{{ formTitle }}</span>
                      </v-card-title>
                      <v-card-text>
                        <v-container>
                          <v-form ref="form" v-model="valid">
                            <v-row>
                              <v-col cols="12" sm="12">
                                <v-text-field v-model="editedItem.n_inventario" label="Inventario" :rules="inventarioRules" required :disabled="editable" ></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="12">
                                <v-text-field v-model="editedItem.nombre" label="nombre" :rules="nombreRules" required ></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="12">
                                <v-select v-model="editedItem.tipo_id" :rules="tipoRules" :items="tipos" item-text="nombre" item-value="id" label="Tipo" required ></v-select>
                              </v-col>
                              <v-col cols="12" sm="12">
                                <v-select v-model="editedItem.id_alumno" :rules="alumnoRules" :items="estudiantes" item-text="nombre" item-value="id" label="Alumno" required ></v-select>
                              </v-col>
                              <v-img :src="fotourl" ></v-img>
                              <v-file-input v-model="editedItem.foto" accept="image/png, image/jpeg, image/bmp" placeholder="Foto del instrumento" prepend-icon="mdi-camera" label="Foto" :required="!editable"></v-file-input>
                              <v-col cols="12" sm="12">
                                <v-switch v-model="elmapita" label="Ver Mapa" ></v-switch>
                                <div style="height: 300px; width: 100%">
                                  <l-map v-if="elmapita" :zoom="zoom" :center="center" :options="mapOptions" style="height: 80%" @update:center="centerUpdate" @update:zoom="zoomUpdate" @click="onMapClick" >
                                    <l-tile-layer :url="url" :attribution="attribution" />
                                    <l-marker v-model="editedItem.marker" :icon="editedItem.marker.icon" :lat-lng="editedItem.marker.latLng" >
                                      <l-popup>{{ editedItem.marker.nombre }}</l-popup>
                                    </l-marker>
                                  </l-map>
                                </div>
                              </v-col>
                            </v-row>
                          </v-form>
                        </v-container>
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                        <v-btn :disabled="!valid" color="blue darken-1" text @click="save" >Cargar</v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-toolbar>
              </template>
              <template v-slot:item.tipo_id="{ item }">{{ getTipo(item.tipo_id) }}</template>
              <template v-slot:item.id_alumno="{ item }">{{ getEstudiante(item.id_alumno) }}</template>
              <template v-slot:item.actions="{ item }">
                <v-icon v-if="permisos.includes('instrumento_update')" small class="mr-2" @click="editItem(item)" dark >mdi-pencil</v-icon >
                <v-icon v-if="permisos.includes('instrumento_borrar')" small @click="deleteItem(item)" dark >mdi-delete</v-icon >
              </template>
            </v-data-table>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </div>
</template>

<script>
import apiOrquesta from "@/services/apiOrquesta.js";
import AppBar from "@/components/AppBar.vue";
import { latLng, icon } from "leaflet";
import { LMap, LTileLayer, LMarker, LPopup } from "vue2-leaflet";

export default {
  title: "OEB - Instrumentos",
  name: "Instrumentos",
  data() {
    return {
      fotourl: "",
      elmapita: false,
      zoom: 13,
      center: latLng(-34.920915, -57.95514),
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      attribution:
        '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      currentZoom: 13,
      currentCenter: latLng(-34.920915, -57.95514),
      violin: icon({
        iconUrl: require("@/assets/violin.png"),
        iconSize: [32, 37],
        iconAnchor: [16, 37],
      }),
      mapOptions: {
        zoomSnap: 0.5,
      },
      showMap: true,
      defaultMarker: {
        id: -1,
        nombre: "ubicacion Seleccionada",
        latLng: latLng(0, 0),
        icon: icon({
          iconUrl: require("@/assets/violin.png"),
          iconSize: [32, 37],
          iconAnchor: [16, 37],
        }),
      },
      valid: false,
      dialog: false,
      headers: [
        { text: "Nombre", align: "start", value: "nombre" },
        { text: "Tipo", align: "start", value: "tipo_id" },
        { text: "Inventario", align: "start", value: "n_inventario" },
        { text: "Alumno", align: "start", value: "id_alumno" },
        { text: "Acciones", value: "actions", sortable: false },
      ],
      permisos: [],
      instrumentos: [],
      estudiantes: [{ id: "0", nombre: "Administracion" }],
      tipos: [
        { id: "1", nombre: "Viento" },
        { id: "2", nombre: "Percusion" },
        { id: "3", nombre: "Cuerda" },
      ],
      editedItem: { id: -1, tipo_id: "", nombre: "", n_inventario: 0, id_alumno: 0, latitud: 0, longitud: 0, foto: " ",
        marker: {
          id: -1,
          nombre: "ubicacion Seleccionada",
          latLng: latLng(0, 0),
          icon: icon({ iconUrl: require("@/assets/violin.png"), iconSize: [32, 37], iconAnchor: [16, 37], }),
        },
      },
      defaultItem: { id: -1, tipo_id: "", nombre: "", n_inventario: 0, id_alumno: 0, latitud: 0, longitud: 0, foto: " ",
        marker: {
          id: -1,
          nombre: "ubicacion Seleccionada",
          latLng: latLng(0, 0),
          icon: icon({ iconUrl: require("@/assets/violin.png"), iconSize: [32, 37], iconAnchor: [16, 37], }),
        }
      },
      editedIndex: -1,
      editable: false,
      inventarioRules: [(v) => !!v || "El numero de inventario es requerido"],
      nombreRules: [(v) => !!v || "Nombre es requerido"],
      tipoRules: [(v) => !!v || "Tipo de Instrumento es requerido"],
      alumnoRules: [(v) => !!v || "Indique quien posee el instrumento"],
    };
  },
  computed: {
    formTitle() {
      return this.editedItem.id === -1 ? "Agregar Instrumentos" : "Editar Instrumentos";
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
  },
  async created() {
    await this.cargarInstrumentos();
    this.cargarPermisos();
  },
  components: {
    AppBar,
    LMap,
    LTileLayer,
    LMarker,
    LPopup,
  },
  methods: {
    async cargarInstrumentos() {
      await apiOrquesta.getEstudiantes().then((response) =>
        response.data.forEach((estudiante) => {
          this.estudiantes.push(estudiante);
        })
      );
      await apiOrquesta
        .getInstrumentos()
        .then((response) => (this.instrumentos = response.data));
    },
    getTipo(idT) {
      if (idT.id) {
        return this.tipos[idT.id - 1].nombre;
      } else {
        return this.tipos[idT - 1].nombre;
      }
    },
    getEstudiante(idE) {
      if (idE == "0") {
        return "Administracion";
      } else {
        if (idE.id) {
          return idE.nombre + " " + idE.apellido; 
        } else {
          var aux = this.estudiantes.filter(function(element) {
            return element.id == idE;
          });
          return aux[0].nombre + " " + aux[0].apellido; 
        }
      }
    },
    editItem(item) {
      this.editedIndex = this.instrumentos.indexOf(item); // guardo el indice del item en el array
      this.editedItem = Object.assign({}, item); 
      this.fotourl = "https://grupo19.proyecto2019.linti.unlp.edu.ar/uploads/" + item.n_inventario + ".jpg"
      this.editedItem.id === -1 ? this.editable = false : this.editable = true; 
      this.editedItem.marker = this.defaultMarker;
      this.editedItem.tipo_id = this.tipos[item.tipo_id - 1];
      if (item.id_alumno == 0) {
        this.editedItem.id_alumno = "{'id': 0, 'nombre': 'Administracion'}";
      } else {
        this.editedItem.id_alumno = this.estudiantes.filter(function(element) {
          return element.id == item.id_alumno;
        })[0];
      }
      this.editedItem.marker.nombre = this.editedItem.nombre;
      this.editedItem.marker.latLng = latLng( this.editedItem.latitud, this.editedItem.longitud );
      this.dialog = true;
    },
    deleteItem(item) {
      const index = this.instrumentos.indexOf(item);
      confirm("Quieres eliminar este instrumento?") &&
        apiOrquesta.deleteInstrumento(item.id).then(() => {
          this.instrumentos.splice(index, 1);
        });
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editable = false
        this.fotourl = ""
      });
    },
    async save() {
      this.$store.commit('activateLoading')
      if (this.editedItem.id > -1) {
        await apiOrquesta.modificarInstrumento(this.editedItem).then(() => {
          Object.assign(this.instrumentos[this.editedIndex], this.editedItem);
          if (this.editedItem.foto){
            apiOrquesta.uploadFile(this.editedItem.foto, this.editedItem.n_inventario);
          }
        })
        .catch((error) => {
          console.log(error)
          this.$store.commit('activeSnack', 'Ocurrio un error, intente de nuevo')
          this.close();
        });
      } else {
        await apiOrquesta.createInstrumento(this.editedItem).then(() => {
          this.editedItem.id = this.instrumentos.length + 1
          this.instrumentos.push(this.editedItem);
          apiOrquesta.uploadFile(this.editedItem.foto, this.editedItem.n_inventario);
        })
        .catch((error) => {
          console.log(error)
          this.$store.commit('activeSnack', 'Ocurrio un error, intente de nuevo')
          this.close();
        })
      }
      this.close();
    },
    zoomUpdate(zoom) {
      this.currentZoom = zoom;
    },
    centerUpdate(center) {
      this.currentCenter = center;
    },
    onMapClick(e) {
      var aux = String(e.latlng);
      aux = aux.replace(")", "");
      aux = aux.replace("LatLng(", "");
      var latLog = aux.split(",");
      this.editedItem.latitud = latLog[0];
      this.editedItem.longitud = latLog[1];
      this.editedItem.marker.latLng = latLng(latLog[0], latLog[1]);
    },
    cargarPermisos() {
      var aux = this.$store.state.user.permisos;
      for (var i = 0; i < aux.length; i++) {
        if (aux[i].nombre.startsWith("instrumento")) {
          this.permisos.push(aux[i].nombre);
        }
      }
    }
  },
};
</script>
