<template>
  <div>
    <AppBar />
    <v-content>
      <v-container class="fill-height" fluid>
        <v-row>
          <v-toolbar-title class="ml-2"><h1>Taller de {{ taller.nombre }}</h1></v-toolbar-title>
        </v-row>
        <v-row align="center" justify="center">
          <v-col>
            <v-data-table :headers="headers" :items="taller.docentes" sort-by="nombre" class="elevation-1" dark>
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>Docentes</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <v-spacer></v-spacer>
                  <v-dialog v-model="dialogM" max-width="500px">
                    <template v-slot:activator="{ on }">
                      <v-btn color="primary" dark class="mb-2" v-on="on">+</v-btn>
                    </template>
                    <v-card>
                      <v-card-title>
                        <span class="headline">Agregar Docente</span>
                      </v-card-title>
                      <v-card-text>
                        <v-container>
                          <v-form ref="form" v-model="valid">
                            <v-row>
                              <v-col cols="12" sm="12" md="12">
                                <v-select v-model="docenteAdd" :rules="docenteRules" :items="taller.docentesNo" item-text="nombreyape" item-value="id" label="Docente" required></v-select>
                              </v-col>
                            </v-row>
                          </v-form>
                        </v-container>
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="closeM">Cancelar</v-btn>
                        <v-btn :disabled="!valid" color="blue darken-1" text @click="agregarDocente">Cargar</v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-toolbar>
              </template>
              <template v-slot:item.actions="{ item }">
                <v-icon v-if="permisos.includes('administrativo_update')" small class="mr-2" @click="editItem(item)" dark >mdi-clock</v-icon>
                <v-icon v-if="permisos.includes('administrativo_update')" small @click="eliminarDocente(item)" dark >mdi-delete</v-icon>
              </template>
            </v-data-table>
          </v-col>
          <v-col>
            <v-data-table :headers="headers" :items="taller.estudiantes" sort-by="nombre" class="elevation-1" dark>
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>Estudiantes</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <v-spacer></v-spacer>
                  <v-dialog v-model="dialogE" max-width="500px">
                    <template v-slot:activator="{ on }">
                      <v-btn color="primary" dark class="mb-2" v-on="on">+</v-btn>
                    </template>
                    <v-card>
                      <v-card-title>
                        <span class="headline">Agregar Estudiante</span>
                      </v-card-title>
                      <v-card-text>
                        <v-container>
                          <v-form ref="form" v-model="valid">
                            <v-row>
                              <v-col cols="12" sm="12" md="12">
                                <v-select v-model="estudiantesAdd" :rules="docenteRules" :items="taller.estudiantesNo" item-text="nombreyape" item-value="id" label="Estudiante" required></v-select>
                              </v-col>
                            </v-row>
                          </v-form>
                        </v-container>
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="closeE">Cancelar</v-btn>
                        <v-btn :disabled="!valid" color="blue darken-1" text @click="agregarEstudiante" >Cargar</v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-toolbar>
              </template>
              <template v-slot:item.actions="{ item }">
                <v-icon v-if="permisos.includes('administrativo_update')" small @click="eliminarEstudiante(item)" dark >mdi-delete</v-icon>
              </template>
            </v-data-table>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
    <v-dialog v-model="dialogH" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Agregar Horario</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form ref="form" v-model="valid">
              <v-row>
                <v-col cols="12" sm="12">
                  <v-select v-model="editedItem.nucleo" :rules="nucleoRules" :items="nucleos" item-text="nombre" item-value="id" label="Nucleo" required></v-select>
                </v-col>
                <v-col cols="12" sm="12">
                  <v-select v-model="editedItem.dia" :rules="diaRules" :items="dias" item-text="nombre" item-value="id" label="Dia" required></v-select>
                </v-col>
                <v-col cols="12" sm="9">
                  <v-menu ref="menu" :close-on-content-click="false" :nudge-right="40" :return-value.sync="editedItem.inicio" transition="scale-transition" offset-x max-width="290px" min-width="290px">
                    <template v-slot:activator="{ on }">
                      <v-text-field v-model="editedItem.inicio" :rules="inicioRules" label="Hora de inicio" prepend-icon="mdi-access_time" readonly v-on="on"></v-text-field>
                    </template>
                    <v-time-picker v-model="editedItem.inicio" full-width @click:minute="$refs.menu.save(editedItem.inicio)"></v-time-picker>
                  </v-menu>
                </v-col>
                <v-col cols="12" sm="9">
                  <v-menu ref="menu2" :close-on-content-click="false" :nudge-right="40" :return-value.sync="editedItem.fin" transition="scale-transition" offset-x max-width="290px" min-width="290px">
                    <template v-slot:activator="{ on }">
                      <v-text-field v-model="editedItem.fin" :rules="finRules" label="Hora de fin" prepend-icon="mdi-access_time" readonly v-on="on"></v-text-field>
                    </template>
                    <v-time-picker v-model="editedItem.fin" full-width @click:minute="$refs.menu2.save(editedItem.fin)"></v-time-picker>
                  </v-menu>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeH">Cancelar</v-btn>
          <v-btn :disabled="!valid" color="blue darken-1" text @click="saveHorario"> Cargar </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-footer app dark>
      <span>&copy; 2020</span>
    </v-footer>
  </div>
</template>

<script>
import apiOrquesta from "@/services/apiOrquesta.js";
import AppBar from "@/components/AppBar.vue";

export default {
  title: "OEB - Talleres",
  name: "Taller",
  data() {
    return {
      doc_id: 0,
      ciclo_id: 0,
      taller_id: 0,
      taller: { id: 0, nombre: "", nombre_corto: "", docentes: [], docentesNo: [], estudiantes: [], estudiantesNo: [], },
      headers: [
        { text: "Nombre", align: "start", value: "nombre" },
        { text: "Apellido", align: "start", value: "apellido" },
        { text: "Acciones", value: "actions", sortable: false },
      ],
      dialogM: false,
      dialogE: false,
      dialogH: false,
      docenteAdd: 0,
      permisos: [],
      estudiantesAdd: 0,
      docenteRules: [(v) => !!v || "Docente es requerido"],
      diaRules: [(v) => !!v || "Seleccione el dia, es requerido"],
      nucleoRules: [(v) => !!v || "Seleccione un nucleo, es requerido"],
      inicioRules: [(v) => !!v || "Horario de inicio es requerido"],
      finRules: [(v) => !!v || "Horario de fin es requerido"],
      valid: false,
      nucleos: [],
      dias: [
        { id: 0, nombre: "" },
        { id: 1, nombre: "Lunes" },
        { id: 2, nombre: "Martes" },
        { id: 3, nombre: "Miercoles" },
        { id: 4, nombre: "Jueves" },
        { id: 5, nombre: "Viernes" },
      ],
      editedItem: { id_drt: -1, inicio: "", fin: "", nucleo: { id: 0, nombre: "sin asignar" }, dia: { id: 0, nombre: "" }, },
      defaultItem: { id_drt: -1, inicio: "", fin: "", nucleo: { id: 0, nombre: "sin asignar" }, dia: { id: 0, nombre: "" }, },
    };
  },
  async created() {
    this.ciclo_id = this.$route.params.cicloId;
    this.taller_id = this.$route.params.tallerId;
    await this.cargarTaller(this.$route.params.cicloId,this.$route.params.tallerId)
    
    this.cargarPermisos();
  },
  components: {
    AppBar
  },
  methods: {
    cargarTaller(ciclo, taller) {
      apiOrquesta.getTaller(ciclo, taller)
      .then(
        (response) => {
          (this.taller = response.data),
          this.taller.docentesNo.forEach((docente) => {
            docente.nombreyape = docente.nombre + " " + docente.apellido;
          }),
          this.taller.estudiantesNo.forEach((estudiante) => {
            estudiante.nombreyape = estudiante.nombre + " " + estudiante.apellido;
          })
          console.log(this.taller)
        }
      )
      apiOrquesta
        .getNucleos()
        .then((response) => (this.nucleos = response.data)); 
    },
    closeM() {
      this.dialogM = false;
      this.$nextTick(() => {
        this.docenteAdd = 0;
      });
    },
    closeE() {
      this.dialogE = false;
      this.$nextTick(() => {
        this.estudiantesAdd = 0;
      });
    },
    closeH() {
      this.dialogH = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
      });
    },
    async agregarDocente() {
      await apiOrquesta
        .addDocenteTaller(this.ciclo_id, this.taller_id, this.docenteAdd)
        .then(() => {
          alert("Docente agregado Correctamente");
          const docente = this.taller.docentesNo.find(element => element.id = this.docenteAdd);
          const index = this.taller.docentesNo.indexOf(docente);
          this.taller.docentesNo.splice(index, 1);
          this.taller.docentes.push(docente)
        });
      this.closeM();
    },
    eliminarDocente(docente) {
      confirm("Eliminar al docente del taller?") &&
        apiOrquesta
          .deleteDocenteTaller(this.ciclo_id, this.taller_id, docente.id)
          .then(() => {
            const docenteD = this.taller.docentes.find(element => element.id = docente);
            const index = this.taller.docentes.indexOf(docenteD);
            this.taller.docentes.splice(index, 1);
            this.taller.docentesNo.push(docenteD)
          });
    },
    async agregarEstudiante() {
      await apiOrquesta
        .addEstudianteTaller(this.ciclo_id, this.taller_id, this.estudiantesAdd)
        .then(() => {
          alert("Estudiante agregado Correctamente");
          const estudiante = this.taller.estudiantesNo.find(element => element.id = this.estudiantesAdd);
          const index = this.taller.estudiantesNo.indexOf(estudiante);
          this.taller.estudiantesNo.splice(index, 1);
          this.taller.estudiantes.push(estudiante)
        });
      this.closeE();
    },
    eliminarEstudiante(estudiante) {
      confirm("Eliminar al estudiante del taller?") &&
        apiOrquesta
          .deleteEstudianteTaller(this.ciclo_id, this.taller_id, estudiante.id)
          .then(() => {
            const estudianteD = this.taller.estudiantes.find(element => element.id = estudiante);
            const index = this.taller.estudiantes.indexOf(estudianteD);
            this.taller.estudiantes.splice(index, 1);
            this.taller.estudiantesNo.push(estudianteD)
          });
    },
    editItem(item) {
      this.editedItem.id_drt = item.id_drt;
      this.dialogH = true;
    },
    saveHorario() {
      apiOrquesta.agregarHorario(this.editedItem);
      this.closeH();
    },
    cargarPermisos() {
      var aux = this.$store.state.user.permisos;
      for (var i = 0; i < aux.length; i++) {
        if (aux[i].nombre.startsWith("administrativo")) {
          this.permisos.push(aux[i].nombre);
        }
      }
    },
  },
};
</script>
