<template >
  <div >
    <AppBar/>
    <v-content >
      <v-container class="fill-height" fluid >
        <v-row  align="center" justify="center">
          <v-col>
            <v-data-table :headers="headers" :items="misTalleres" sort-by="nombre" class="elevation-1" dark>
              <template v-slot:top>
                <v-toolbar flat >
                  <v-toolbar-title>Mis cursos</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <v-spacer></v-spacer>
                </v-toolbar>
              </template>
              <template v-slot:item.actions="{ item }">
                <v-icon small @click="showWorkShop(item.ciclo_lectivo_id, item.taller_id)" class="mr-4" dark>mdi-eye</v-icon>
                <v-icon small @click="showAsistencia(item.ciclo_lectivo_id, item.taller_id, item.docente_id)">mdi-hand-right</v-icon>
              </template>
            </v-data-table>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
    <v-footer app dark>
      <span>&copy; 2020</span>
    </v-footer>
  </div>
</template> 

<script>
import apiOrquesta from '@/services/apiOrquesta.js'
import AppBar from '@/components/AppBar.vue';

export default {
  title: "OEB - Dashboard",
  name: "Dash",
  data() {
    return {
      misTalleres:[],
      headers: [
        {text: 'Nombre',align: 'start', value: 'nombre'},
        {text: 'Nombre corto',align: 'start', value: 'nombre_corto'},
        {text: 'Acciones', value: 'actions', sortable: false},
      ]
    }
  },
  components: {
    AppBar
  },
  async created(){
    if(this.$store.state.user.email){
      await this.cargarDocente()
    }
  },
  computed:{
    user: function(){ return this.$store.state.user.email}
  },
  watch: {
    user: function () {
        this.cargarDocente()
    }
  },
  methods: {
    cargarDocente(){
      apiOrquesta.getDocente(this.$store.state.user.email)
      .then(response => (this.misTalleres = response.data.talleres))
      .catch(error => console.log(error))
    },
    showWorkShop(ciclo, taller){
      this.$router.push({ path: '/verTaller/'+ciclo+'/'+taller })
    },
    showAsistencia(ciclo, taller, id){
      this.$router.push({ path: '/verAsistencia/'+ciclo+'/'+taller+'/'+id })
    }
  }
};
</script>