<template >
  <div >
    <AppBar/>
    <v-content >
      <v-container class="fill-height" fluid >
        <v-row  align="center" justify="center">
          <v-col>
            <v-data-table :headers="headers" :items="estudiantes" sort-by="nombre" class="elevation-1" dark>
              <template v-slot:top>
                <v-toolbar flat >
                  <v-toolbar-title>Asistencia</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <v-spacer></v-spacer>
                </v-toolbar>
              </template>
              <template v-slot:item.actions="{ item }">
                <v-icon small @click="editItem(item)">mdi-hand-right</v-icon>
              </template>
            </v-data-table>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Asistencia de {{editedItem.nombre}} {{editedItem.apellido}}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form ref="form">
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-select v-model="editedItem.estado" :items="estados" item-text="nombre" item-value="id" label="Asistencia" required></v-select>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
         <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
          <v-btn :disabled="!valid" color="blue darken-1" text @click="save">Cargar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-footer app dark>
      <span>&copy; 2020</span>
    </v-footer>
  </div>
</template> 

<script>
import apiOrquesta from '@/services/apiOrquesta.js'
import AppBar from '@/components/AppBar.vue';

export default {
  title: "OEB - Asistencia",
  name: "Dash",
  data() {
    return {
      valid: true,
      dialog: false,
      ciclo_id: '',
      taller_id: '',
      taller: '',
      id_drt: '',
      fecha: '',
      asistencia:[],
      estudiantes:[],
      estados:[{id: 0, nombre: 'sin pasar'}, {id: 1, nombre: 'presente'}, {id: 2, nombre: 'ausente'}, {id: 3 , nombre: 'justificada'}, {id: 4 , nombre: 'no cursa'}],
      headers: [
        {text: 'Nombre',align: 'start', value: 'nombre'},
        {text: 'Apellido',align: 'start', value: 'apellido'},
        {text: 'Asistencia',align: 'start', value: 'estado.nombre'},
        {text: 'Acciones', value: 'actions', sortable: false},
      ],
      defaultItem:{id: -1, nombre: '', apellido: '', estado: {id: 0, nombre: 'sin pasar'}},
      editedItem:{id: -1, nombre: '', apellido: '', estado: {id: 0, nombre: 'sin pasar'}}
    }
  },
  components: {
    AppBar
  },
  created(){
    this.ciclo_id = this.$route.params.ciclo
    this.taller_id = this.$route.params.taller
    var aux = this.$route.params.fecha.split(' ')
    this.fecha = aux[0]
    this.id_drt = this.$route.params.id_drt
    this.cargarAsistencia()
  }, 
  methods: {
    cargarAsistencia(){
      apiOrquesta.getTaller(this.ciclo_id, this.taller_id)
      .then(response => (this.taller = response.data))
      .then( () => {
        apiOrquesta.getAsistencias(this.ciclo_id, this.taller_id, this.fecha)
        .then(response => (this.asistencia = response.data))
        .then( () => {
          this.taller.estudiantes.forEach(estudiante => {
            this.estudiantes.push({
              id: estudiante.id,
              nombre: estudiante.nombre,
              apellido: estudiante.apellido,
              estado: this.getEstado(estudiante.id)
            })
          })
        })
      });
    },
    getEstado(id){
      var aux = 
        this.asistencia.filter(function(element) {
          return element.id == id;
        });
        if (aux[0]) {
          return this.estados[aux[0].estado]
        } else {
          return this.estados[0]
        }
    },
    editItem (item) {
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    close () { 
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
      })
    },
    save () {
      apiOrquesta.pasarAsistencia(this.editedItem, this.fecha, this.id_drt)
      .then(() => {
        this.estudiantes = []
        this.cargarAsistencia()
      })
      this.close()
    }
  }
};
</script>