<template>
  <div>
    <AppBar/>
    <v-content >
      <v-container class="fill-height" fluid >
        <v-row  align="center" justify="center">
          <v-col>
            <v-toolbar-title class="my-2">Taller de {{ taller.taller }} año {{taller.year}} semestre {{taller.semestre}} dictado por {{taller.profesor}}</v-toolbar-title>
            <v-sheet tile height="54" color="grey lighten-3" class="d-flex">
              <v-btn icon class="ma-2" @click="$refs.calendar.prev()">
                <v-icon>mdi-chevron-left</v-icon>
              </v-btn>
              <v-toolbar-title>{{ title }}</v-toolbar-title>
              <v-btn icon class="ma-2" @click="$refs.calendar.next()">
                <v-icon>mdi-chevron-right</v-icon>
              </v-btn>
            </v-sheet>
            <v-sheet height="600">
              <v-calendar ref="calendar" v-model="value" :events="mostrar" @change="updateRange" @click:event="goAsis" ></v-calendar>
            </v-sheet>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
    <v-footer app dark>
      <span>&copy; 2020</span>
    </v-footer>
  </div>
</template>

<script>
  import apiOrquesta from '@/services/apiOrquesta.js'
  import AppBar from '@/components/AppBar.vue';
  export default {
    title: "OEB - Asistencia",
    data(){
      return{
        type: 'mounth',
        value: '',
        start: null,
        end: null,
        mostrar:[],
        docente_id: 0,
        ciclo_id: 0,
        taller_id: 0,
        taller:{ profesor: "", taller: "", fecha_ini: "", fecha_fin: "", year: 0, semestre: 0, horarios: [] } 
      }
    },
    components:{
      AppBar,
      //Calendario
    },
    computed: {
      title () {
        const { start, end } = this
        if (!start || !end) {
          return ''
        }
        const startMonth = this.monthFormatter(start)
        const startYear = start.year
        return `${startMonth} ${startYear}`
      },
      monthFormatter () {
        return this.$refs.calendar.getFormatter({
          timeZone: 'UTC', month: 'long',
        })
      },
    }, 
    created(){
      this.ciclo_id = this.$route.params.cicloId
      this.taller_id = this.$route.params.tallerId
      this.docente_id = this.$route.params.docenteId
      this.cargarHorarios(this.ciclo_id, this.taller_id, this.docente_id)
    },
    mounted () {
      this.$refs.calendar.checkChange()
    },
    methods:{
      cargarHorarios(ciclo, taller, docente){
        apiOrquesta.getHorarios(ciclo, taller, docente).then(response => (
          this.taller = response.data,
          this.taller.horarios.forEach(horario => {
            this.getDiasDeHorario(horario)
          })));
      },
      getDiasDeHorario(horario){
        var aux = this.taller.fecha_ini.split('-')
        var inicio = new Date(aux[2], aux[1] , aux[1]);
        aux = this.taller.fecha_fin.split('-')
        var fin = new Date(aux[2], aux[1] , aux[0]);
        while (inicio <= fin) {
          if (inicio.getDay() == horario.dia){
            var hIni = horario.inicio.split(':')
            var hFin = horario.fin.split(':')
            this.mostrar.push({
              name: horario.nombre,
              start: `${inicio.getFullYear()}-${inicio.getMonth() + 1}-${inicio.getDate()} ${hIni[0]}:${hIni[1]}`,
              end: `${inicio.getFullYear()}-${inicio.getMonth() + 1}-${inicio.getDate()} ${hFin[0]}:${hFin[1]}`,
              color: 'green',
            })
          }
          inicio.setDate(inicio.getDate() + 1); 
        }
      },
      updateRange ({ start, end }) {
        this.start = start
        this.end = end
      },
      goAsis({event}){
        var aux = event.start.split('T')
        this.$router.push({ path: '/pasarAsistencia/'+this.ciclo_id+'/'+this.taller_id+'/'+this.taller.horarios[0].taller_responsable+'/'+aux[0] })
      }
    }
  }
</script>