<template>
  <div>
    <h4 class="my-2">Talleres del {{ ciclo.semestre }} semestre del año {{ciclo.year}}</h4>
    <v-data-table :headers="headers" :items="ciclo.talleres" sort-by="nombre" class="elevation-4 my-4" dark :hide-default-footer="true" >
      <template v-slot:item.actions="{ item }">
        <v-icon small @click="showWorkShop(ciclo.id, item.id)" class="ml-2" dark>mdi-eye</v-icon>
      </template>
    </v-data-table>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        headers: [
          {text: 'Nombre',align: 'start', value: 'nombre'},
          {text: 'Nombre corto',align: 'start', value: 'nombre_corto'},
          {text: 'Acciones', value: 'actions', sortable: false},
        ] 
      }
    },
    props:{
      ciclo:{
        id: 0,
        year: 0,
        semestre: 0,
        talleres:[]
      }
    },
    methods:{
      showWorkShop(ciclo, taller){
        this.$router.push({ path: '/verTaller/'+ciclo+'/'+taller })
      }
    }
  }
</script>