<template>
  <v-content class="content">
    <v-container class="fill-height" fluid>
      <v-row align="center" justify="center">
        <v-col cols="12" sm="8" md="4">
          <v-card class="elevation-12" dark>
            <v-toolbar flat>
              <v-toolbar-title>Iniciar Sesion</v-toolbar-title>
              <v-spacer />
            </v-toolbar>
            <v-card-text align="center" justify="center">
              <v-img class="mb-2" src="@/assets/OEB.png" max-height="300" max-width="200"></v-img>
              <v-btn class="my-3" large @click="conGoogle()">
                <v-icon class="mr-4">mdi-google</v-icon>Iniciar Session con Google
              </v-btn>
              <v-divider></v-divider>
              <v-form @submit.prevent="login()">
                <v-text-field label="E-mail" name="login" type="email" v-model="email" />
                <v-text-field
                  label="Contraseña"
                  name="password"
                  type="password"
                  v-model="password"
                />
                <div class="d-flex flex-row-reverse">
                  <v-btn color="yellow" type="submit" light>Login</v-btn>
                </div>
              </v-form>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-content>
</template>

<script>
import apiOrquesta from "@/services/apiOrquesta.js";

export default {
  title: "OEB - Login",
  name: "Home",
  data() {
    return {
      email: "",
      password: ""
    };
  },
  created() {},
  methods: {
    async login() {
      await apiOrquesta.login(this.email, this.password)
      .then(response => {
        this.$store
        .dispatch('login', response.data.token)
        .then(() => {
          this.$router.push({ name: "Dash" })
        })
      })
      .catch(error => {
        if(error.response.status == 401){
          alert('usuario o contraseña equivocada')
        }
        if (error.response.status == 403) {
          alert('usuario inactivo, comunicarse con el administrador para que lo active')
        }})
    },
    conGoogle() {
      window.location.href =
        "https://grupo19.proyecto2019.linti.unlp.edu.ar/login_google";
    }
  }
};
</script>

<style scoped>
.content {
  background-image: url(../assets/loginbgd.jpg);
  background-size: cover;
}
</style>