<template>
  <div>
    <AppBar />
    <v-content>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col>
            <v-data-table :headers="headers" :items="ciclos" sort-by="year" class="elevation-1" dark :expanded.sync="expanded" show-expand >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>Ciclos lectivos</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <v-spacer></v-spacer>
                  <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on }">
                      <v-btn color="primary" dark class="mb-2" v-on="on">+</v-btn>
                    </template>
                    <v-card>
                      <v-card-title>
                        <span class="headline"> {{ formTitle }} </span>
                      </v-card-title>
                      <v-card-text>
                        <v-container>
                          <v-form ref="form" v-model="valid">
                            <v-row>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.year" label="Año" :rules="añoRules" required ></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-text-field v-model="editedItem.semestre" label="Semestre" :rules="semestreRules" required ></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-menu ref="menu1" v-model="menu1" :close-on-content-click="false" transition="scale-transition" offset-y max-width="290px" min-width="290px" >
                                  <template v-slot:activator="{ on }">
                                    <v-text-field v-model="editedItem.fecha_ini" label="Fecha inicio" hint="MM/DD/YYYY format" persistent-hint v-on="on" ></v-text-field>
                                  </template>
                                  <v-date-picker v-model="editedItem.fecha_ini" no-title @input="menu1 = false" :rules="inicioRules" required ></v-date-picker>
                                </v-menu>
                              </v-col>
                              <v-col cols="12" sm="6" md="4">
                                <v-menu ref="menu2" v-model="menu2" :close-on-content-click="false" transition="scale-transition" offset-y max-width="290px" min-width="290px" >
                                  <template v-slot:activator="{ on }">
                                    <v-text-field v-model="editedItem.fecha_fin" label="Fecha fin" hint="MM/DD/YYYY format" persistent-hint v-on="on" ></v-text-field>
                                  </template>
                                  <v-date-picker v-model="editedItem.fecha_fin" no-title @input="menu2 = false" :rules="finRules" required ></v-date-picker>
                                </v-menu>
                              </v-col>
                            </v-row>
                          </v-form>
                        </v-container>
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                        <v-btn :disabled="!valid" color="blue darken-1" text @click="save" >Cargar</v-btn >
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-toolbar>
              </template>
              <template v-slot:item.actions="{ item }">
                <v-icon v-if="permisos.includes('administrativo_update')" small class="mr-2" @click="addTaller(item)" dark > mdi-plus</v-icon >
              </template>
              <template v-slot:expanded-item="{ item }">
                <td :colspan="headers.length">
                  <TablaTalleresAuxiliar :ciclo="item" />
                </td>
              </template>
            </v-data-table>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
    <v-footer app dark>
      <span>&copy; 2020</span>
    </v-footer>
    <v-dialog v-model="addT" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline"> Agregar Taller </span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form ref="form" v-model="valid">
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedTaller.taller_id" :rules="añoRules" :items="tal" item-text="nombre" item-value="id" label="Taller" required ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedTaller.year" label="Año" :rules="añoRules" disabled required ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedTaller.semestre" label="Semestre" :rules="semestreRules" disabled required ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeT">Cancelar</v-btn>
          <v-btn :disabled="!valid" color="blue darken-1" text @click="saveT" >Cargar</v-btn >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import apiOrquesta from "@/services/apiOrquesta.js";
import AppBar from "@/components/AppBar.vue";
import TablaTalleresAuxiliar from "@/components/TablaTalleresAuxiliar.vue";

export default {
  title: "OEB - Ciclos",
  name: "Ciclos",
  data() {
    return {
      expanded: [],
      dateFormatted: this.formatDate(new Date().toISOString().substr(0, 10)),
      menu1: false,
      menu2: false,
      valid: false,
      dialog: false,
      addT: false,
      headers: [
        { text: "Año", align: "start", value: "year" },
        { text: "Semestre", align: "start", value: "semestre" },
        { text: "Inicio", align: "start", value: "fecha_ini" },
        { text: "Fin", align: "start", value: "fecha_fin" },
        { text: "Acciones", value: "actions", sortable: false },
        { text: "", value: "data-table-expand" },
      ],
      ciclos: [],
      permisos: [],
      tal: [],
      editedItem: { id: -1, year: 0, semestre: 0, fecha_ini: "", fecha_fin: "" },
      defaultItem: { id: -1, year: 0, semestre: 0, fecha_ini: "", fecha_fin: "" },
      editedIndex: -1,
      editedTaller: { taller_id: -1, year: 0, semestre: 0 },
      defaultTaller: { taller_id: -1, year: 0, semestre: 0 },
      añoRules: [(v) => !!v || "Año es requerido"],
      semestreRules: [(v) => !!v || "Semestre es requerido"],
      inicioRules: [(v) => !!v || "fecha de inicio es requerido"],
      finRules: [(v) => !!v || "Fecha de fin es requerido"],
    };
  },
  computed: {
    formTitle() {
      return this.editedItem.id === -1 ? "Agregar Ciclo" : "Editar Ciclo";
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    addT(val) {
      val || this.closeT();
    }
  },
  async created() {
    await this.cargarCiclos();
    this.cargarPermisos();
  },
  components: {
    AppBar,
    TablaTalleresAuxiliar
  },
  methods: {
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${month}/${day}/${year}`;
    },
    parseDate(date) {
      if (!date) return null;
      const [month, day, year] = date.split("/");
      return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
    },
    cargarCiclos() {
      apiOrquesta.getCiclos().then((response) => (this.ciclos = response.data));
      apiOrquesta.getTalleresN().then((response) => (this.tal = response.data));
    },
    editItem(item) {
      this.editedIndex = this.ciclos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
      this.dateFormatted = this.formatDate(
        new Date(item.fecha_ini).toISOString().substr(0, 10)
      );
      this.editedItem.fecha_ini = this.parseDate(this.dateFormatted);
      this.dateFormatted = this.formatDate(
        new Date(item.fecha_fin).toISOString().substr(0, 10)
      );
      this.editedItem.fecha_fin = this.parseDate(this.dateFormatted);
    },
    addTaller(item) {
      this.editedIndex = this.ciclos.indexOf(item);
      this.editedTaller = Object.assign({}, this.defaultTaller);
      this.editedTaller.year = item.year;
      this.editedTaller.semestre = item.semestre;
      this.addT = true;
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
      });
    },
    closeT() {
      this.addT = false;
      this.$nextTick(() => {
        this.editedTaller = Object.assign({}, this.defaultTaller);
      });
    },
    async save() {
      this.$store.commit('activateLoading')
      if (this.editedItem.id > -1) {
        await apiOrquesta.modificarCiclo(this.editedItem).then(() => {
          Object.assign(this.ciclos[this.editedIndex], this.editedItem);
        })
        .catch((error) => {
          console.log(error)
          this.$store.commit('activeSnack', 'Ocurrio un error, intente de nuevo')
          this.close();
        });
      } else {
        await apiOrquesta
          .createCiclo(this.editedItem)
          .then(() => {
            this.ciclos.push(this.editedItem);
          })
          .catch((error) => {
            console.log(error) 
            this.$store.commit('activeSnack', 'Cilo ya existe')
            this.close();
          });
      }
      this.$store.commit('deactivateLoading')
      this.close();
    },
    async saveT() {
      this.$store.commit('activateLoading')
      console.log(this.editedTaller)
      console.log(this.tal)
      await apiOrquesta.createTaller(this.editedTaller)
      .then(() => {
        this.editedTaller.id = this.editedTaller.taller_id
        this.editedTaller.nombre = this.tal[this.editedTaller.taller_id - 1].nombre
        this.editedTaller.nombre_corto = this.tal[this.editedTaller.taller_id - 1].nombre_corto
        this.ciclos[this.editedIndex].talleres.push(this.editedTaller);
        this.$store.commit('activeSnack', 'Taller creado exitosamente')
      })
      .catch((error) => {
        console.log(error)
        this.$store.commit('activeSnack', 'Ocurrio un error, intente de nuevo')
        this.closeT();
      });
      this.$store.commit('deactivateLoading')
      this.closeT();
    },
    cargarPermisos() {
      var aux = this.$store.state.user.permisos;
      for (var i = 0; i < aux.length; i++) {
        if (aux[i].nombre.startsWith("administrativo")) {
          this.permisos.push(aux[i].nombre);
        }
      }
    },
  },
};
</script>
