<template>
  <div>
    <AppBar />
    <v-content>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col>
            <v-data-table :headers="headers" :items="usuarios" sort-by="nombre" class="elevation-1" dark >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>Usuarios</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <v-spacer></v-spacer>
                  <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on }">
                      <v-btn color="primary" dark class="mb-2" v-on="on" >+</v-btn >
                    </template>
                    <v-card>
                      <v-card-title>
                        <span class="headline"> {{ formTitle }} </span>
                      </v-card-title>
                      <v-card-text>
                        <v-container>
                          <v-form ref="form" v-model="valid">
                            <v-row>
                              <v-col cols="12" sm="12">
                                <v-text-field v-model="editedItem.firstname" label="Nombre" :rules="nombreRules" required ></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="12">
                                <v-text-field v-model="editedItem.lastname" label="Apellido" :rules="apellidoRules" required ></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="12">
                                <v-text-field v-model="editedItem.email" label="Email" :rules="emailRules" required ></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="12">
                                <v-checkbox v-model="editedItem.inprofesor" label="Profesor" disabled ></v-checkbox>
                                <v-checkbox v-model="editedItem.inadministrador" label="Administrador" ></v-checkbox>
                                <v-checkbox v-model="editedItem.inpreceptor" label="Preceptor" ></v-checkbox>
                              </v-col>
                            </v-row>
                          </v-form>
                        </v-container>
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn v-if="soy(editedItem.email)" color="blue darken-1" text @click="cambio">Cambiar Contraseña</v-btn>
                        <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                        <v-btn :disabled="!valid" color="blue darken-1" text @click="save" >Cargar</v-btn >
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-toolbar>
              </template>
              <template v-slot:item.roles="{ item }">
                <p v-for="rol in item.roles" :key="rol.id">{{ rol.nombre }}</p>
              </template>
              <template v-slot:item.actions="{ item }">
                <div v-if="!soy(item.email)">  
                  <v-icon v-if="(item.activo == 'Activo')" small class="mr-2" @click="toggleUser(item)" dark >mdi-account-multiple-minus</v-icon >
                  <v-icon v-else small class="mr-2" @click="toggleUser(item)" dark>mdi-account-multiple-plus</v-icon>
                </div>
                <v-icon v-if="permisos.includes('usuario_modificar')" small class="mr-2" @click="editItem(item)" dark>mdi-pencil</v-icon>
                <v-icon v-else-if="soy(item.email)" small class="mr-2" @click="editItem(item)" dark >mdi-pencil</v-icon >
                <v-icon v-if="permisos.includes('usuario_borrar')" small @click="deleteItem(item)" dark >mdi-delete</v-icon>
              </template>
            </v-data-table>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
    <v-dialog v-model="dialogCon" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Cambiar Contraseña</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form ref="form" v-model="validCon">
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field v-model="contraseña" label="Contraseña nueva" :rules="passRules" required></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeCon">Cancelar</v-btn>
          <v-btn :disabled="!validCon" color="blue darken-1" text @click="cambiarContra">Cambiar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-footer app dark>
      <span>&copy; 2020</span>
    </v-footer>
  </div>
</template>

<script>
import apiOrquesta from "@/services/apiOrquesta.js";
import AppBar from "@/components/AppBar.vue";

export default {
  title: "OEB - Usuarios",
  name: "Usuarios",
  data() {
    return {
      valid: false,
      dialog: false,
      validCon: false,
      dialogCon: false,
      contraseña: '',
      headers: [
        { text: "Nombre", align: "start", value: "firstname" },
        { text: "Apellido", align: "start", value: "lastname" },
        { text: "E-mail", align: "start", value: "email" },
        { text: "Roles", align: "start", value: "roles" },
        { text: "Estado", align: "start", value: "activo" },
        { text: "Acciones", value: "actions", sortable: false },
      ],
      usuarios: [],
      permisos: [],
      editedItem: { id: -1, lastname: "", firstname: "", email: "", emailPrev: "", inprofesor: false, inadministrador: false, inpreceptor: false, },
      defaultItem: { id: -1, lastname: "", firstname: "", email: "", emailPrev: "", inprofesor: false, inadministrador: false, inpreceptor: false, },  
      editedIndex: -1,
      emailRules: [
        (v) => !!v || "E-mail es requerido",
        (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
      ],
      nombreRules: [(v) => !!v || "Nombre es requerido"],
      apellidoRules: [(v) => !!v || "Apellido es requerido"],
      passRules: [(v) => !!v || "La contraseña es requerida es requerido"],
    };
  },
  computed: {
    formTitle() {
      return this.editedItem.id === -1 ? "Agregar Usuarios" : "Editar Usuarios";
    },
    soyYo(email){
      return this.$store.state.user.email == email
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  async created() {
    await this.cargarUsuarios();
    this.cargarPermisos();
  },
  components: {
    AppBar
  },
  methods: {
    soy(email){
      return this.$store.state.user.email == email
    },
    async cargarUsuarios() {
      await apiOrquesta
      .getUsuarios()
      .then((response) => {
        this.usuarios = response.data;
        this.usuarios.forEach(user => {
          if (user.activo == 1){
            user.activo = "Activo"
          }
          else{
            user.activo = "Desactivado"
          }
        });
      })
      .catch(() => {
        if (this.$store.state.user.activo == 1){
            this.$store.state.user.activo = "Activo"
          }
          else{
            this.$store.state.user.activo = "Desactivado"
          }
        this.usuarios.push(this.$store.state.user)
      });
    },
    async toggleUser(item){
      let opt = "";
      if (item.activo == "Activo"){
        opt = "desactivar";
        item.activo = "Desactivado"
      }
      else{
        opt = "activar";
        item.activo = "Activo"
      }
      await apiOrquesta.toggleUsuario(item.id, opt).then((response) => {
        console.log(response)
        this.$store.commit('activeSnack', `El usuario fue ${item.activo}`)
      })
    },
    tieneRol(roles, rol) {
      var aux = roles.filter(function(element) {
        return element.id == rol;
      })[0];
      if (aux) {
        return true;
      } else {
        return false;
      }
    },
    editItem(item) {
      this.editedIndex = this.usuarios.indexOf(item);
      this.editedItem = Object.assign({}, item);
      item.inadministrador ? this.editedItem.inadministrador = item.inadministrador : this.editedItem.inadministrador = this.tieneRol(item.roles, 1);
      item.inpreceptor ? this.editedItem.inpreceptor = item.inpreceptor : this.editedItem.inpreceptor = this.tieneRol(item.roles, 2);
      item.inprofesor ? this.editedItem.inprofesor = item.inprofesor : this.editedItem.inprofesor = this.tieneRol(item.roles, 3);
      this.editedItem.emailPrev = item.email;
      this.dialog = true;
    },
    deleteItem(item) {
      const index = this.usuarios.indexOf(item);
      confirm("Eliminar al usuario " + item.email) &&
        apiOrquesta.deleteUsuario(item.email).then(() => {
          this.usuarios.splice(index, 1);
        });
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
      });
    },
    cambio(){
      this.close();
      this.dialogCon = true;
    },
    closeCon() {
      this.dialogCon = false;
      this.$nextTick(() => {
        this.contraseña = '';
      });
    },
    async cambiarContra() {
      await apiOrquesta
        .cambiarContra(this.contraseña, this.$store.state.user.email)
        .then(() => {
          this.$store.commit('activeSnack', 'Contraseña cambiada con exito')
        })
        .catch((error) => {
          console.log(error)
          this.$store.commit('activeSnack', 'Ocurrio un error, intente de nuevo')
          this.closeCon();
        });
      this.closeCon();
    },
    async save() {
      this.$store.commit('activateLoading')
      if (this.editedItem.id > -1) {
        await apiOrquesta.modificarUsuario(this.editedItem).then(() => {
          let roles = []
          if (this.editedItem.inadministrador) {
            roles.push({
              id: 1,
              nombre: 'administrador'
            })
          }
          if (this.editedItem.inpreceptor) {
            roles.push({
              id: 2,
              nombre: 'preceptor'
            })
          }
          if (this.editedItem.inprofesor) {
            roles.push({
              id: 3,
              nombre: 'profesor'
            })
          }
          this.editedItem.roles = roles
          Object.assign(this.usuarios[this.editedIndex], this.editedItem);
        })
        .catch((error) => {
          console.log(error)
          this.$store.commit('activeSnack', 'Ocurrio un error, intente de nuevo')
          this.close();
        });
      } else {
        await apiOrquesta.createUsuario(this.editedItem).then(() => {
          let roles = []
          if (this.editedItem.inadministrador) {
            roles.push({
              id: 1,
              nombre: 'administrador'
            })
          }
          if (this.editedItem.inpreceptor) {
            roles.push({
              id: 2,
              nombre: 'preceptor'
            })
          }
          if (this.editedItem.inprofesor) {
            roles.push({
              id: 3,
              nombre: 'profesor'
            })
          }
          this.editedItem.roles = roles
          this.editedItem.id = this.usuarios.length + 1 
          this.editedItem.emailPrev = this.editedItem.email
          this.usuarios.push(this.editedItem);
        })
        .catch((error) => {
          console.log(error)
          this.$store.commit('activeSnack', 'Ocurrio un error, intente de nuevo')
          this.close();
        })
      }
      this.$store.commit('deactivateLoading')
      this.close();
    },
    cargarPermisos() {
      var aux = this.$store.state.user.permisos;
      for (var i = 0; i < aux.length; i++) {
        if (aux[i].nombre.startsWith("usuario")) {
          this.permisos.push(aux[i].nombre);
        }
      }
    },
  },
};
</script>
